/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * System imports
 ============================================================================ */
import React from "react";
import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../../../submitButton";
import Card from "../../../card";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  // const {} = props.barrelGetters;
  const { setRedirectTo } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Return
     ============================================================================ */
  return (
    <Card
      backgroundHeader="Transparent"
      backgroundBody="Transparent"
      border="0"
      header={false}
    >
      <div className="bd-masthead">
        <h1>Pievienojies vietējo valūtu tirgiem</h1>

        <p className="lead">
          Ātri izveido un piekļūsti tirgus vidēm, izmantojot vietni
          Local-Currency.com, kas ir pasaulē pirmā vietējās tirdzniecības
          platforma, kurā ir viena klikšķa dienas nauda, biznesa rīku komplekts,
          tūlītēji pārskaitījumi un jaudīga valūtas pārvaldība.
        </p>

        <br />

        <Container fluid>
          <Row>
            <Col style={{ paddingLeft: 0 }}>
              <SubmitButton
                {...args}
                caption="Pievienojies"
                request="get started"
                namespace="LogIn"
                onSubmit={() => setRedirectTo("/sign-up")}
              />
            </Col>
            <Col style={{ paddingRight: 0 }}>
              <SubmitButton
                {...args}
                caption="Ievads"
                request="get started"
                namespace="LogIn"
                variant="secondary"
                onSubmit={() => setRedirectTo("/article/readme")}
              />
            </Col>
          </Row>
        </Container>

        <br />
      </div>
    </Card>
  );
};
